import $ from "jquery";
import React, {Component} from "react";

export default class Main extends Component {
    state = {
        unprocessedLines: [],
        linesInQueueToResubmit: [],
        uploadQueue: [],
        warnings: [],
        loading: "",
        enqueuedFiles: [],
        table: "",
        dateOfSubmission: "",
        awardNo: "",
        lineNum: 1,
        totalLineNums: 0,
    };

    static getLoadingImage(size, bgColor) {
        if (!size) {
            size = 64;
        }
        const style =  {
            width: size+"px",
            height: size+"px",
        };
        if (bgColor) {
            style['backgroundImage'] = 'radial-gradient('+bgColor+', #ffffff00)';
        }
        const srcUrl = Main.getPageUrl("loading.gif");
        return <img src={srcUrl} style={style} alt='loading'/>;
    }

    getProcessingMessage(mssg) {
        const img = Main.getLoadingImage();
        return <p className='centered'>{img}<br />{mssg}...</p>;
    }

    static getPageUrl(localUrl) {
        const params = Main.getUrlVars();
        if (params['page']) {
          const parts = [];
          parts.push("pid="+params['pid']);
          if (localUrl) {
            const page = "reporting/upload_react/run/"+localUrl;
            const pageWithoutPHPSuffix = page.replace(/\.php$/, "");
            parts.push("page="+encodeURIComponent(pageWithoutPHPSuffix));
          } else if (params['page']) {
            parts.push("page="+encodeURIComponent(params['page']));
          }
          if (params['prefix']) {
            parts.push("prefix="+encodeURIComponent(params['prefix']));
          }
          return "?"+parts.join("&");
        }
        return localUrl;
      }
  
    static getUrlVars() {
        const vars = {};
        window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
          vars[key] = value;
        });
        return vars;
    }  

    static makePostRequest(query) {
        query['redcap_csrf_token'] = window.getCSRFToken();
        const formBody = [];
        for (const property in query) {
          const encodedKey = encodeURIComponent(property);
          const encodedValue = encodeURIComponent(query[property]);
          formBody.push(encodedKey + "=" + encodedValue);
        }
  
        return {
          method: 'post',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
          },
          body: formBody.join("&")
        };
    }
  
    static verifyFields(fields) {
        for (let i=0; i < fields.length; i++) {
            const fieldId = fields[i];
            if (($(fieldId).length === 0) || !$(fieldId).val()) {
                return false;
            }
        }
        return true;
    }

    static eliminateColumn(ary, colName) {
        for (let i=0; i < ary.length; i++) {
            if (typeof ary[i][colName] !== "undefined") {
                delete ary[i][colName];
            }
        }
        return ary;
    }

    static formDataToJSON(formData) {
        const object = {};
        formData.forEach(function(value, key){
            object[key] = value;
        });
        return JSON.stringify(object);
    }

    submitMainForm() {
        const files = this.state.enqueuedFiles;
        if (
            Main.verifyFields(['#table', '#file', '#dateOfSubmission', '#awardNo'])
            && (files.length > 0)
        ) {
            const table = $('#table').val();
            const dateOfSubmission = $('#dateOfSubmission').val();
            const awardNo = $('#awardNo').val();
            const mssg = (table === 5) ? "Parsing File and Looking Up Publications" : "Parsing File";
            this.setState({
                ...this.state,
                loading: this.getProcessingMessage(mssg),
                unprocessedLines: [],
                uploadQueue: [],
                warnings: [],
                table: table,
                awardNo: awardNo,
                dateOfSubmission: dateOfSubmission,
                lineNum: 1,
                doNotUpdate: false,
            }, () => {
                this.checkIfReadyToRead();
            });
        } else {
            alert('Not all fields are valid');
        }
    }

    checkIfReadyToRead() {
        const files = this.state.enqueuedFiles;
        
        const formData = new FormData();
        formData.append('file', files[0]);
        formData.append('action', 'getNumLines');
        formData.append('table', this.state.table);
        formData.append('redcap_csrf_token', window.getCSRFToken());

        const link = this.props.link;
        const requestOptions = {
            method: 'POST',
            body: formData
        };

        return new Promise((resolve, reject) => {
          fetch(link, requestOptions)
          .then(response => Main.processResponse(response))
          .then(items => {
            if (typeof items !== "object") {
                this.processError(items);
            } else if (typeof items.error != "undefined") {
                this.processError(items.error);
            } else if (this.state.lineNum <= items.numLines) {
                this.uploadFileToReadNextLine();
                this.setState({
                    ...this.state,
                    totalLineNums: items.numLines,
                });
            } else {
                this.setState({
                    ...this.state,
                    enqueuedFiles: [],
                    totalLineNums: 0,
                    loading: "",
                    doNotUpdate: false,
                    lineNum: 1,
                });    
            }
            resolve(true);
          })
          .catch(error => this.processError(error));
        });
    }

    compileSelects() {
        const selects = {};
        $('select option:checked').each((idx, ob) => {
            const name = $(ob).attr("name");
            const value = $(ob).val();
            selects[name] = value;
        });
        return selects;
    }

    uploadChangesToReadNextLine(lines, lineNum) {
        if (lineNum <= lines.length) {     // the PHP code subtracts 1 from lineNum to adjust to no headers
            const query = {
                lines: lines,
                dateOfSubmission: this.state.dateOfSubmission,
                awardNo: this.state.awardNo,
                table: this.state.table,
                action: 'processLines',
                scope: lineNum,
                doNotUpdate: false,
                lineNum: lineNum,
                selects: this.compileSelects(),
            };
            const queryWithoutLines = { ...query };
            delete queryWithoutLines.lines;
            const link = this.props.link;
            const requestOptions = Main.makePostRequest(query);
            console.log(JSON.stringify(queryWithoutLines));
            return new Promise((resolve, reject) => {
                fetch(link, requestOptions)
                .then(response => Main.processResponse(response))
                .then(items => {
                    console.log(JSON.stringify(items));
                    if (typeof items !== "object") {
                        this.processError(items);
                    } else if (typeof items.error != "undefined") {
                        this.processError(items.error);
                    } else {
                        const newLines = items.lines ?? [];
                        const newUpload = items.upload ?? [];
                        const warnings = items.warnings ?? [];
                        const nextLineNum = lineNum + 1;
                        const unprocessedLines = [...this.state.unprocessedLines, ...newLines];
        
                        this.setState({
                            ...this.state,
                            unprocessedLines: unprocessedLines,
                            uploadQueue: [...this.state.uploadQueue, ...newUpload],
                            warnings: [...this.state.warnings, ...warnings],
                            lineNum: nextLineNum,
                            doNotUpdate: false,
                        }, () => {
                            this.uploadChangesToReadNextLine(lines, nextLineNum);
                        });
                    }
                    resolve(true);
                })
                .catch(error => this.processError(error));
            });      
        } else {
            this.setState({
                ...this.state,
                totalLineNums: 0,
                lineNum: 1,
                loading: "",
                doNotUpdate: false,
                linesInQueueToResubmit: [],
            });
        }
    }

    static isResponseJSON(response) {
        return (response.headers.get('content-type')?.indexOf('application/json') >= 0);
    }

    static processResponse(response) {
        if (Main.isResponseJSON(response)) {
            return response.json();
        } else {
            return response.text();
        }
    }

    uploadFileToReadNextLine() {
        const files = this.state.enqueuedFiles;
        const lineNum = this.state.lineNum;

        const formData = new FormData();
        formData.append('file', files[0]);
        formData.append('dateOfSubmission', this.state.dateOfSubmission);
        formData.append('awardNo', this.state.awardNo);
        formData.append('table', this.state.table);
        formData.append('action', 'uploadFile');
        formData.append('scope', lineNum);
        formData.append('redcap_csrf_token', window.getCSRFToken());

        const link = this.props.link;
        const requestOptions = {
            method: 'POST',
            body: formData
        };

        return new Promise((resolve, reject) => {
          fetch(link, requestOptions)
          .then(response => Main.processResponse(response))
          .then(items => {
            console.log(JSON.stringify(items));
            if (typeof items !== "object") {
              this.processError(items);
            } else if (typeof items.error != "undefined") {
              this.processError(items.error);
            } else {
                const newLines = items.lines ?? [];
                const newUpload = items.upload ?? [];
                const warnings = items.warnings ?? [];

                this.setState({
                    ...this.state,
                    unprocessedLines: [...this.state.unprocessedLines, ...newLines],
                    uploadQueue: [...this.state.uploadQueue, ...newUpload],
                    warnings: [...this.state.warnings, ...warnings],
                    lineNum: lineNum + 1,
                }, () => {
                    this.checkIfReadyToRead();
                });
            }
            resolve(true);
          })
          .catch(error => this.processError(error));
        });            
    }

    enqueueFile(event) {
        this.setState({
            ...this.state,
            enqueuedFiles: event.target.files,
            doNotUpdate: false,
        });
    }

    processError(error) {
        if (typeof error.message !== "undefined") {
            console.log("ERROR: "+error.message);
        }
        console.error(error);
        const lines = this.state.linesInQueueToResubmit;
        this.setState({
          ...this.state,
          loading: "",
          doNotUpdate: false,
          totalLineNums: 0,
          lineNum: 1,
          linesInQueueToResubmit: [],
          unprocessedLines: lines,
        });
        let mssg = "General failure";
        if (
            error.toString().match(/Unexpected token/)
            || error.toString().match(/<DOCTYPE/)
        ) {
            mssg = 'Unable to access database to get data. (You might need to login to REDCap again, or you might have run out of processing time.)';
        } else {
            mssg = 'Unable to access database to get data. '+mssg;
        }
        alert(mssg);
    }

    uploadNow() {
        if (this.state.uploadQueue.length > 0) {
            const link = this.props.link;
            const query = {
                action: "uploadREDCap",
                upload: this.state.uploadQueue,
                table: this.state.table,
                awardNo: this.state.awardNo,
                dateOfSubmission: this.state.dateOfSubmission,
            };
            const requestOptions = Main.makePostRequest(query);
            this.setState({
                ...this.state,
                loading: this.getProcessingMessage("Uploading to REDCap"),
                doNotUpdate: false,
            });
            console.log(JSON.stringify(query));
            return new Promise((resolve, reject) => {
                fetch(link, requestOptions)
                .then(response => Main.processResponse(response))
                .then(items => {
                  if (typeof items !== "object") {
                    this.processError(items);
                  } else if (typeof items.error != "undefined") {
                    this.processError(items.error);
                  } else {
                    this.setState({
                      ...this.state,
                      unprocessedLines: [],
                      uploadQueue: [],
                      warnings: [],
                      loading: "",
                      doNotUpdate: false,
                    });
                    alert('Uploaded!');
                  }
                  resolve(true);
                })
                .catch(error => this.processError(error));
            });              
        }
    }

    makeUploadButton() {
        if ((this.state.uploadQueue.length > 0) && (this.state.unprocessedLines.length === 0) && (this.state.totalLineNums === 0)) {
            const cnt = this.state.uploadQueue.length;
            const rowWord = (cnt > 1) ? "Rows" : "Row";
            return <div id='bigBlueButton' onClick={this.uploadNow.bind(this)}>
                <h1>Upload {cnt} {rowWord} Now</h1>
            </div>;
        } else {
            console.log("unprocessedLines: "+JSON.stringify(this.state.unprocessedLines));
            const statusStyle = {
                maxWidth: "400pt",
                marginLeft: "auto",
                marginRight: "auto",
                paddingBottom: "10px",
            };
            const img = Main.getLoadingImage(32, "#ffffff88");
            const numRemaining = this.state.totalLineNums - this.state.lineNum;
            const rowWord = (numRemaining === 1) ? "row" : "rows";
            const updateText = (numRemaining > 0) ? <span>{numRemaining} more {rowWord} of {this.state.totalLineNums} total</span> : "Last row";
            const parsingMssg = <table className="centered">
                <tbody>
                    <tr>
                        <td className="bigger bolded padded">Processing...</td>
                        <td>{img}</td>
                        <td className="bigger bolded padded">{updateText}</td>
                    </tr>
                </tbody>
            </table>;
            const tableText = this.state.table ? <span>Table {this.state.table}<br/></span> : "";
            const parsingText = (this.state.totalLineNums > 0) ? parsingMssg : "";
            const numComments = this.getNumberOfComments();
            const issueWord = (numComments === 1) ? "issue" : "issues";
            return <div className='centered green' style={statusStyle}>
                <h2>Status</h2>
                <h3>Upload File &rarr; Adjudicate &rarr; Upload to REDCap</h3>
                {tableText}
                Remaining Work: {numComments} {issueWord}<br/>
                REDCap Rows Ready to Upload: {this.state.uploadQueue.length}<br/>
                <span className='smaller'>(Uploads complete only at end of adjudication.)</span>
                {parsingText}
            </div>;
        }
    }

    getNumberOfComments() {
        let numComments = 0;
        for (let i=0; i < this.state.unprocessedLines.length; i++) {
            const line = this.state.unprocessedLines[i];
            if (typeof line.comments !== "undefined") {
                numComments += Object.keys(line.comments).length;
            }
        }
        return numComments;
    }

    // Coordinated with REDCapManagement::makeHTMLId
    static makeHTMLId(id) {
        if (!id) {
            return "";
        }
        id = id.replace(/[\s-]+/g, "_");
        id = id.replace(/<[^>]+>/g, "");
        id = id.replace(/[:+"/[\]'#<>~`!@#$%\^&*()=;?.,]/g, "");
        return id;
    }

    shouldComponentUpdate(nextProps, nextState) { 
        if (nextState.doNotUpdate) { 
          return false;
        }
        return true;
    }
    
    onChangePosition(lineID, selectID, positionType, newValue) {
        if (this.state.unprocessedLines.length > lineID) {
            const changedValue = $('#'+selectID+' option:selected').val();
            const startIndex = (this.state.table === "8C") ? 1 : 0;
            const colID = (positionType === "initial") ? 6 + startIndex : 7 + startIndex;
            const oldValue = this.state.unprocessedLines[lineID][colID];
            const positionFields = this.getPositionFields();

            const nodes = (oldValue.split(/\s*\/\s*/).length === positionFields.length) ? oldValue.split(/\s*\/\s*/) : this.makeBlankArray(positionFields.length);
            for (let i=0; i < positionFields.length; i++) {
                if ((nodes[i] == newValue) && (changedValue !== positionFields[i])) {
                    nodes[i] = "";
                } else if (changedValue == positionFields[i]) {
                    nodes[i] = newValue;
                }
            }
            const newPosition = nodes.join(" / ");
            this.updateLine(lineID, colID, newPosition);
        }
    }

    makeBlankArray(numNodes) {
        const ary = [];
        for (let i=0; i < numNodes; i++) {
            ary.push("");
        }
        return ary;
    }

    onChangeDate(lineID, dateID, startOrEnd) {
        if (this.state.unprocessedLines.length > lineID) {
            const changedValue = $('#'+dateID).val();
            const colID = 3;   // for Table 5 Training Period

            const oldValue = this.state.unprocessedLines[lineID][colID];
            let value = '';
            if (!startOrEnd) {
                value = changedValue;
            } else if (startOrEnd === "start") {
                const nodes = oldValue.split(/-/);
                nodes[0] = changedValue;
                value = nodes.join('-');
            } else if (startOrEnd === "end") {
                const nodes = oldValue.split(/-/);
                if (nodes.length > 1) {
                    nodes[1] = changedValue;
                }
                value = nodes.join('-');
            } else {
                // this should never happen
            }
            if (dateID.match(/_date$/)) {
                const stringID = dateID.replace(/_date$/, '_string');
                if ($('#'+stringID).length > 0) {
                    $('#'+stringID).val(value);
                }
            } else if (dateID.match(/_string$/)) {
                const dateFieldID = dateID.replace(/_string$/, '_date');
                if ($('#'+dateFieldID).length > 0) {
                    const nodes = value.split(/-/);
                    const label = $('label[for='+dateFieldID+']').text();
                    let node = value;
                    if (label.match(/\bend\b/i)) {
                        if (nodes.length > 1) {
                            node = nodes[1];
                        } else {
                            node = nodes[0];
                        }
                    } else if (label.match(/\bstart\b/i)) {
                        node = nodes[0];
                    }
                    $('#'+dateFieldID).val(node);
                }
            }
            this.updateLine(lineID, colID, value);
        }        
    }

    updateLine(lineID, colID, value) {
        const lines = this.state.unprocessedLines;
        lines[lineID][colID] = value;
        
        this.setState({
            ...this.state,
            unprocessedLines: lines,
            doNotUpdate: true,
        });
    }

    onChangeRecord(lineID, record) {
        if (this.state.unprocessedLines.length > lineID) {
            const colID = 'record_id';
            this.updateLine(lineID, colID, record);
        }
    }

    getTraineeName(line) {
        if (line.length === 0) {
            return "";
        } else if ((this.state.table === "8A") || (this.state.table === "8C")) {
            return line[0];
        } else if ((this.state.table === "5A") || (this.state.table === "5B")) {
            return line[1];
        }
        // Tables 2 & 4 have no trainee name
        return "";
    }

    getFacultyName(line) {
        if (line.length === 0) {
            return "";
        } else if ((this.state.table === "8A") || (this.state.table === "8C")) {
            return (this.state.table === "8C") ? line[2] : line[1];
        } else if ((this.state.table === "5A") || (this.state.table === "5B")) {
            return line[0];
        } else if (this.state.table === "2") {
            return line[0];
        } else if (this.state.table === "4") {
            return line[0]
        }
        return "";
    }

    getIdentifier(traineeName, facultyName) {
        const reformattedTraineeName = traineeName ? traineeName : "[none]";
        return <span className='bigger'>
            Trainee: <strong>{reformattedTraineeName}</strong>; Faculty: <strong>{facultyName}</strong>
        </span>;
    }

    getNumberOfWarnings() {
        let cnt = 0;
        for (let i=0; i < this.state.warnings.length; i++) {
            cnt += Object.keys(this.state.warnings[i]).length;
        }
        return cnt;
    }

    static makeWarningComp(ary, identifier, id) {
        return (<div key={id} className='centered yellow'>
            {identifier}<br/>
            {ary.note ?? ''}<br/>
            <ul>
                {ary.titles.map((title) => { return <li>{title}</li>; })}
            </ul>
        </div>);
    }

    getPositionFields() {
        return [
            "title",
            "field",
            "institution",
            "sector",
            "activity"
        ];
    }
      
    processRows() {
        if (this.state.unprocessedLines.length + this.state.warnings.length > 0) {
            if (this.getNumberOfComments() + this.getNumberOfWarnings() === 0) {
                return "";
            } else {
                let hasYesNo = false;
                const commentList = [];
                const counterStyle = { padding: "2px"};
                for (let i=0; i < this.state.warnings.length; i++) {
                    const warningHash = this.state.warnings[i];
                    for (let key in warningHash) {
                        const traineeName = warningHash[key].trainee;
                        const facultyName = warningHash[key].faculty;
                        const identifier = this.getIdentifier(traineeName, facultyName);
                        const id = Main.makeHTMLId(traineeName+"_"+facultyName+"_"+key);
                        commentList.push(Main.makeWarningComp(warningHash[key], identifier, id));
                    }
                }
                for (let i=0; i < this.state.unprocessedLines.length; i++) {
                    const line = this.state.unprocessedLines[i];
                    if (typeof line.comments !== "undefined") {
                        const traineeName = this.getTraineeName(line);
                        const facultyName = this.getFacultyName(line);
                        const identifier = this.getIdentifier(traineeName, facultyName);
                        const id = Main.makeHTMLId(traineeName+"_"+facultyName);
                        let counter = 0;
                        let lineTotal = Object.keys(line.comments).length;
                        for (let issue in line.comments) {
                            counter++;
                            const location = <span className='bigger bolded blue' style={counterStyle}>#{counter} of {lineTotal}</span>;
                            const keyID = id+"_key_"+i+"_"+issue;
                            const comment = line.comments[issue];
                            if (comment.match(/More than one record matched: /)) {
                                const recordStr = comment.replace(/More than one record matched: /, '');
                                const records = recordStr.split(/\s*,\s*/);
                                const name = 'record_id_'+id;
                                commentList.push(<div key={keyID} className='centered'>
                                    {location}&nbsp;
                                    {identifier}<br/>
                                    {comment}<br/>
                                    {records.map(rec => {
                                        const myID = id+'_'+rec;
                                        return <span>
                                            <input type='radio' key={myID} name={name} onChange={this.onChangeRecord.bind(this, i, rec)} id={myID} value={rec} />&nbsp;
                                            <label htmlFor={myID}>Record {rec}</label>
                                            <br/>
                                        </span>;
                                    })}
                                </div>);
                            } else if (comment.match(/create a record/)) {
                                const yesID = id+"_yes";
                                const yesValue = "new";
                                const noID = id+"_no";
                                const noValue = "skip";
                                const name = 'record_id_'+id;
                                hasYesNo = true;

                                commentList.push(<div key={keyID} className='centered'>
                                    {location}&nbsp;
                                    {identifier}<br/>
                                    {comment}<br/>
                                    <input type='radio' key={yesID} name={name} linenum={i} onChange={this.onChangeRecord.bind(this, i, yesValue)} value={yesValue} id={yesID} />&nbsp;
                                    <label htmlFor={yesID}>Yes</label><br/>
                                    <input type='radio' key={noID} name={name} linenum={i} onChange={this.onChangeRecord.bind(this, i, noValue)} value={noValue} id={noID} />&nbsp;
                                    <label htmlFor={noID}>No</label>
                                </div>);
                            } else if ((issue === "Initial Position") || (issue === "Current Position")) {
                                const fields = this.getPositionFields();
                                const shortIssue = issue.replace(/ Position$/, '').toLowerCase();

                                const splitLine = comment.split(/\?/);
                                if (splitLine.length >= 2) {
                                    const question = splitLine[0]+"?";
                                    const data = (splitLine[1] !== "") ? splitLine[1].trim().split(/\s*\/\s*/) : [];
    
                                    const selects = [];
                                    for (let j=0; j < data.length; j++) {
                                        const elem = data[j].trim();
                                        if (elem) {
                                            const selectId = id+'___'+shortIssue+'___'+i+"_"+j;
                                            selects.push(<p>
                                                Map '{elem}' to&nbsp;
                                                <select key={selectId} id={selectId} name={selectId} onChange={this.onChangePosition.bind(this, i, selectId, shortIssue, elem)}>
                                                    <option value=''>---SELECT---</option>
                                                    {fields.map(field => {
                                                        const descript = (field === "title") ? "Position Title" : field.charAt(0).toUpperCase() + field.slice(1);
                                                        return <option value={field}>{descript}</option>;
                                                    })}
                                                </select>
                                            </p>);
                                        }
                                    }
    
                                    if (selects.length > 0) {
                                        commentList.push(<div key={keyID} className='centered'>
                                            {location}&nbsp;
                                            {identifier}<br/>
                                            {question}
                                            {selects.map(selectDiv => { return selectDiv; })}
                                        </div>);
                                    }
                                }
                            } else if (issue === "Training Period") {
                                const dateID = id+"_date";
                                const stringID = id+"_string";
                                let trainingPeriodHTML = '';
                                const nodes = line[3].split(/-/);
                                const start = nodes[0];
                                const end = (nodes.length > 1) ? nodes[1] : nodes[0];
                                if (comment.match(/end year/i)) {
                                    trainingPeriodHTML = <span>
                                        <label htmlFor={dateID}>End of Training Period (Year)</label>&nbsp;
                                        <input type='text' key={dateID} id={dateID} linenum={i} tail='end' onChange={this.onChangeDate.bind(this, i, dateID, "end")} defaultValue={end} /><br/>
                                        <label htmlFor={stringID}>Entire Training Period Field</label>&nbsp;
                                        <input type='text' key={stringID} id={stringID} linenum={i} tail='' onChange={this.onChangeDate.bind(this, i, stringID, '')} defaultValue={line[3]} />
                                    </span>;
                                } else if (comment.match(/start year/)) {
                                    trainingPeriodHTML = <span>
                                        <label htmlFor={dateID}>Start of Training Period (Year)</label>&nbsp;
                                        <input type='text' key={dateID} id={dateID} linenum={i} tail='start' onChange={this.onChangeDate.bind(this, i, dateID, "start")} defaultValue={start} /><br/>
                                        <label htmlFor={stringID}>Entire Training Period Field</label>&nbsp;
                                        <input type='text' key={stringID} id={stringID} linenum={i} tail='' onChange={this.onChangeDate.bind(this, i, stringID, '')} defaultValue={line[3]} />
                                    </span>;
                                } else {
                                    trainingPeriodHTML = <span>
                                        <label htmlFor={stringID}>Training Period:</label>&nbsp;
                                        <input type='text' key={stringID} id={stringID} linenum={i} tail='' onChange={this.onChangeDate.bind(this, i, stringID, '')} defaultValue={line[3]} />
                                    </span>;
                                }
                                commentList.push(<div key={keyID} className='centered'>
                                    {location}&nbsp;
                                    {identifier}<br/>
                                    {comment}<br/>
                                    {trainingPeriodHTML}
                                </div>);
                            } else if (issue === "Exception") {
                                commentList.push(<div key={keyID} className='centered'>
                                    {location}&nbsp;
                                    {identifier}<br/>
                                    {comment}<br/>
                                    Please correct in the original CSV.
                                </div>);
                            } else {
                                commentList.push(<div key={keyID} className='centered'>
                                    {location}&nbsp;
                                    {identifier}<br/>
                                    {comment}<br/>
                                    Please correct in the original CSV.
                                </div>);
                            }
                        }
                    }
                }

                const resubmitButton = (this.getNumberOfComments() > 0) ? <div className='centered' key='commentsButton'>
                    <p className='centered'>Tip: Double-check that you are logged into REDCap before clicking this button.</p>
                    <button onClick={this.submitComments.bind(this)}>Submit Changes</button>
                </div> : "";

                const spacer = <span>&nbsp;&nbsp;&nbsp;</span>;
                const resubmitDisplay = (this.state.loading === "") ? resubmitButton : "";
                const presentMessage = "Some year fields record a date as 'Present.' Since this is time-relative, these values need to be updated when uploading them in the future.";
                const messageStyle = {
                    maxWidth: '250px',
                    float: 'left'
                };
                const prolegomena = hasYesNo ? <div>
                    <p className='centered'>
                        <button onClick={this.checkAll.bind(this, "yes")}>Check Each 'Yes'</button>
                        {spacer}
                        <button onClick={this.checkAll.bind(this, "no")}>Check Each 'No'</button>
                    </p>
                    <div style={messageStyle} className='smaller'>{presentMessage}</div>
                    <div style={messageStyle}><button onClick={this.removePresents.bind(this)}>Remove Each Year That's 'Present'</button></div>
                </div> : "";
            
                return <div id='comments'>
                    {prolegomena}
                    {commentList.map(comment => { return comment; })}
                    {resubmitDisplay}
                </div>
            }
        } else if (this.state.uploadQueue.length === 0) {
            return <p key='begin' className='centered bolded'>Upload a CSV Below to Begin</p>;
        } else {
            return "";
        }
    }

    removePresents() {
        $('input[type=text]').each((idx, ob) => {
            const id = $(ob).attr('id');
            if (id.match(/_date$/) || id.match(/_string$/)) {
                const value = $(ob).val();
                const newValue = value.replace(/Present/, '');
                if (value != newValue) {
                    const tail = $(ob).attr("tail");
                    const i = $(ob).attr("linenum");
                    $(ob).val(newValue);
                    this.onChangeDate(i, id, tail)
                }
            }
        });
    }

    checkAll(value) {
        const regex = new RegExp("_"+value+"$");
        $('input[type=radio]').each((idx, ob) => {
            const id = $(ob).attr("id");
            if (id.match(regex)) {
                const name = $(ob).attr("name");
                const i = $(ob).attr("linenum");
                const value = $(ob).val();
                $('input[type=radio][name='+name+']').attr("checked", false);
                $(ob).attr("checked", true);
                this.onChangeRecord(i, value)
            }
        });
    }

    submitComments() {
        const unprocessedLines = this.state.unprocessedLines;
        this.setState({
            ...this.state,
            totalLineNums: unprocessedLines.length,
            loading: this.getProcessingMessage("Submitting Changes and Looking up More Publications"),
            doNotUpdate: false,
            unprocessedLines: [],
            linesInQueueToResubmit: unprocessedLines,
            warnings: [],
            lineNum: 1,
        }, () => {
            this.uploadChangesToReadNextLine(unprocessedLines, 1);
        });
    }

    render() {
        const processText = this.processRows();
        const uploadButton = this.makeUploadButton();
        const mainDisplay = <section>
                <h2>Start a New Upload</h2>
                <p className="centered">
                    <label htmlFor="table">Table:</label>&nbsp;
                    <select id="table" name="table" defaultValue=''>
                        <option value="">---SELECT---</option>
                        <option value="2">Table 2 - Participating Faculty Members</option>
                        <option value="4">Table 4 - Research Support of Participating Faculty Members</option>
                        <option value="5A">Table 5A - Publications: Predoctoral</option>
                        <option value="5B">Table 5B - Publications: Postdoctoral</option>
                        <option value="8A">Table 8A - Program Outcomes: Predoctoral</option>
                        <option value="8C">Table 8C - Program Outcomes: Postdoctoral</option>
                    </select>
                </p>
                <p className="centered">
                    <label htmlFor="dateOfSubmission">Date When These Tables Were Submitted:</label>&nbsp;
                    <input type="date" id="dateOfSubmission" name="dateOfSubmission" />
                </p>
                <p className="centered">
                    <label htmlFor="awardNo">What is the Award Number for this Grant?</label>&nbsp;
                    <input type="text" id="awardNo" name="awardNo" />
                </p>
                <p className="centered">
                    <label htmlFor="file">File with Table as CSV (Comma-Separated Values):</label>&nbsp;
                    <input type="file" id="file" name="file" onChange={this.enqueueFile.bind(this)} />
                </p>
                <p className="centered"><button onClick={this.submitMainForm.bind(this)}>Upload CSV</button></p>
        </section>;
        const display = this.state.loading ? this.state.loading : mainDisplay;
        return (
            <main className='max-width'>
                <h1>Upload Prior Years to Flight Tracker:<br/>Tables 2, 4, 5 &amp; 8</h1>
                <p className='centered'>Convert prior years' data into Flight Tracker. Warning: Automatically looking up the publications for Table 5 can take some time. Please use the headings available from <a href='https://grants.nih.gov/grants/funding/datatables-g/Consolidated_Training_Tables.pdf'>default NIH Training Tables</a> in CSV format.</p>
                <p className='centered'>From Microsoft Word, you can copy &amp; paste the table into Excel. Make sure that the headers are in the first row. Then save as a CSV (Comma-Separated Values). Then upload to Flight Tracker and follow its instructions.</p>
                {uploadButton}
                {processText}

                {display}
            </main>
        );
    }
}